<template>
<div>
    <el-col :span="2" class="attr-name-right">
    {{ attrName }}
    </el-col>
    <el-col :span="4" style="padding-bottom:10px;">
    <el-input class="attr-input" v-model="inputValue" @blur="handleInput(inputValue)"></el-input>
    <el-upload
              v-show="up_show_flag"
              class="avatar-uploader attr-upload"
              :data="uploadParam"
              action="http://img.orkotech.com/fdfs_upload.php"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
    <img v-if="imageUrl" :src="imageUrl" class="avatar">
    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
    </el-col>
</div>
</template>
<script>

export default {
    name: 'attrinput',
    data () {
        return {
            imageUrl: '',
            inputValue: '',
            uploadParam: {
                file_field: "file",//文件上传字段 将文件流保存到该值（eg:fdfs_file）
                token: "",//加密token md5( 加密salt值 + 13位时间戳 )
                time: +new Date(),//13位时间戳
                format: "",//上传的文件格式
                file: null//文件流
            },
        }
    },
    props: {
        attrName: {
            type: String,
            default: "白色"
        },
        index: {
            type: Number,
            default: 0
        },
        up_show_flag: {
            type: Boolean,
            default: false,
        },
    },
    mounted() {
    },
    destroyed() {
    },
    methods:{
        handleAvatarSuccess(response,file) {
            if (response.status == 0) {
                this.imageUrl = "http://img.orkotech.com/" + response.path;
            } else {
                this.$message.error(response.msg);
            }
        },
        beforeAvatarUpload(file) {
            //const isJPG = file.type === 'image/jpeg';
            //if (!isJPG) {
            //    this.$message.error('上传头像图片只能是 JPG 格式!');
            //}
            //const isLt2M = file.size / 1024 / 1024 < 2;
            //if (!isLt2M) {
            //    this.$message.error('上传头像图片大小不能超过 2MB!');
            //}
            //return isJPG && isLt2M;

            this.uploadParam.token = this.$md5('salt001'+this.uploadParam.time.toString());
            let fileType = file.type;
            this.uploadParam.format = fileType.slice(fileType.indexOf('/')+1);
            this.uploadParam.file = file;
        },
        handleInput(val) {
            this.$emit('get_input', {value:val, cn:this.attrName, index:this.index});
        },
    }
}
</script>
<style>
.attr-name-right{
    padding-top:8px;
    font-size:14px;
    text-align:right;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}
.avatar-uploader-icon {
    color: #8c939d;
    width: 34px;
    height: 34px;
    line-height: 34px;
    text-align: center;
    float:right;
}
.avatar {
    width: 34px;
    height: 34px;
    display: block;
}
.attr-input{
    width:70%;
}
.attr-upload{
    width:28%;height:35px;float:right;
}
.avatar:hover{
    width:150px;
    height:150px;
    position:relative;
    z-index:997;
}
</style>



