<template>
<div>
    <script :id="id" type="text/plain"></script>
</div>
</template>
<script>
import '../../../static/UE/ueditor.config.js'
import '../../../static/UE/ueditor.all.min.js'
import '../../../static/UE/lang/zh-cn/zh-cn.js'
import '../../../static/UE/ueditor.parse.min.js'
export default {
    name: 'UE',
    data () {
        return {
            editor: null,
            config: {
                initialFrameWidth: null,
                initialFrameHeight: 300,
                toolbars: [[
                    'fullscreen', 'source', '|', 'undo', 'redo', '|',
                    'emotion', 'bold', 'italic', 'underline', 'fontborder', 'strikethrough', '|',
                    'forecolor', 'backcolor', 'fontfamily', 'fontsize',
                    'justifyleft', 'justifycenter', 'justifyright', '|',
                    'simpleupload','insertvideo','|',
                    'horizontal',
                    'selectall','removeformat', 'cleardoc',
                ]],
                elementPathEnabled: false,
                wordCount: false,

            },
        }
    },
    props: {
        id: {
            type: String,
            default: "editor"
        },
        content: {
            type: String,
            default: ""
        }
    },
    mounted() {
        this.editor = UE.getEditor(this.id, this.config); // 初始化UE
        const _this = this;
        this.editor = UE.getEditor(this.id, this.config);
        this.editor.addListener("ready", function(){
            _this.editor.setContent(_this.content);
        });
    },
    destroyed() {
        this.editor.destroy();
    },
    methods:{
        getUEContent() {
            return this.editor.getContent();
        },
        setUEContent(data) {
            return this.editor.setContent(data);
        }
    }
}
</script>



