<template>
  <div style="height:100%;">
    <div class="main-container">
      <div class="main-div">
        <div class="main-content-c">
          <div style="margin-left:15%;width:60%;">
            <el-steps :active="2" finish-status="success" align-center>
              <el-step title="1. 产品排重检查已完成"></el-step>
              <el-step title="2. 产品信息录入已完成"></el-step>
              <el-step title="3. 发布产品进行中"></el-step>
            </el-steps>
          </div>
          <el-form :model="data" :rules="rules" ref="data">
          <p class="p-title">发布信息</p>
          <p class="p-content-main">
            <el-row :gutter="20">
              <el-col :span="2" class="name-right">发布国家</el-col>
              <el-col :span="9">
                <el-select v-model="data.country" placeholder="请选择" style="width:100%;">
                  <el-option
                    v-for="item in countryList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="2" class="name-right">产品名称</el-col>
              <el-col :span="9">
                <el-input v-model="data.name" placeholder="请输入内容" size="medium" ></el-input>
              </el-col>
            </el-row>
          </p>
          <p class="p-content-main">
            <el-row :gutter="20">
              <el-col :span="2" class="name-right">模板选择</el-col>
              <el-col :span="9">
                <el-select v-model="data.temp_type" placeholder="请选择" style="width:100%;">
                  <el-option
                    v-for="item in templateList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="2" class="name-right">发布域名</el-col>
              <el-col :span="9">
                <el-input v-model="data.domain" placeholder="请输入内容" size="medium" style="width:40%;"></el-input>
                <el-select v-model="data.host" placeholder="请选择" style="width:50%;" size="medium">
                  <el-option
                    v-for="item in wwwList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </p>
          <p class="p-content-main">
            <el-row :gutter="20">
              <el-col :span="2" class="name-right">发布语言</el-col>
              <el-col :span="9">
                <el-select v-model="data.language" placeholder="请选择" style="width:100%;">
                  <el-option
                    v-for="item in langList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </p>
          <br>
          <p class="p-title">规格信息
            &nbsp;&nbsp;&nbsp;&nbsp;
            <el-checkbox v-model="checked" v-on:click.native="showUpload()" ref="ele_up">图片选项</el-checkbox>
          </p>
          <p class="p-content-main">
            <el-row :gutter="20" style="width:94%;">
              <attrinput v-for="(item, index) in attrInputList" :attrName="item.value" :index="index"
                         @get_input="get_value"
                         :up_show_flag="item.up_show_flag"></attrinput>
            </el-row>
          </p>
          <p class="p-content-main">
            <el-row style="z-index:1;">
              <el-col :span="1">&nbsp;</el-col>
              <el-col :span="1"><p class="attr-title">序号</p></el-col>
              <el-col :span="3"><p class="attr-title">规格值</p></el-col>
              <el-col :span="3"><p class="attr-title">SKU编码</p></el-col>
              <el-col :span="3"><p class="attr-title">销售价</p></el-col>
              <el-col :span="4">
                <p class="attr-title" style="text-align:left;">
                  &nbsp;&nbsp;
                  <el-checkbox v-on:click.native="releaseAll()" ref="ele_rel" :checked="releaseAllFlag"
                               v-model="releaseAllFlag">全部发布
                  </el-checkbox>
                  <el-checkbox v-on:click.native="promotionSet()" ref="ele_set">全部促销设置</el-checkbox>
                </p>
              </el-col>
              <el-col :span="5">
                <p class="attr-title">促销信息</p>
              </el-col>
            </el-row>
            <el-row v-model="attrRowList" class="row-one" v-for="(item, index) in attrRowList">
              <el-col :span="1">&nbsp;</el-col>
              <el-col :span="1">
                <p class="attr-one">{{ index+1 }}</p>
              </el-col>
              <el-col :span="3">
                <p class="attr-one">{{ item.sku_attribute }}</p>
              </el-col>
              <el-col :span="3">
                <p class="attr-one">{{ item.sku_code }}</p>
              </el-col>
              <el-col :span="3">
                <el-input v-model="item.input" v-on:change="checkPrice(item)"></el-input>
              </el-col>
              <el-col :span="4">
                &nbsp;&nbsp;
                <el-checkbox style="line-height:40px;" :checked="item.flag" class="release-item">发布&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-checkbox>
                <el-checkbox style="line-height:40px;" class="release-item-set" ref="promotion_set"
                             @click.native="clickSinglePromotionSet(index)">促销设置
                </el-checkbox>
              </el-col>
              <el-col :span="5" style="text-align:center;line-height:40px;">
                <span style="font-size:14px;" v-model="item.info">{{item.info ? item.info : '无'}}</span>
              </el-col>
            </el-row>
          </p>

          <p class="p-title">产品图片</p>
          <p class="p-content-main">
            <el-row :gutter="20">
              <el-col :span="24">
                <el-upload
                  action="http://img.orkotech.com/fdfs_upload.php"
                  :limit=5
                  :data="uploadParam"
                  :before-upload="beforeUpload"
                  :on-success="uploadSuccess"
                  :on-error="uploadFail"
                  list-type="picture-card"
                  :on-exceed="overLimit"
                  :on-preview="handlePictureCardPreview"
                  :on-remove="handleRemove">
                  <i class="el-icon-plus"></i>
                </el-upload>
                <el-dialog :visible.sync="dialogVisible">
                  <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>
              </el-col>
            </el-row>
          </p>
          <br>

          <p class="p-title">产品描述</p>
          <p class="p-content-main">
            <el-row :gutter="20">
              <el-col :span="22">
                <el-tabs type="border-card">
                  <el-tab-pane label="产品详情">
                    <UE id="editor" ref="ue1"></UE>
                  </el-tab-pane>
                  <el-tab-pane label="产品参数">
                    <UE id="editor2" ref="ue2"></UE>
                  </el-tab-pane>
                </el-tabs>
              </el-col>
            </el-row>
          </p>
          <br>
        </el-form>
        </div>

        <el-dialog title="促销设置" :visible.sync="dialogFormVisible" width="30%">
          <el-form :model="form">
            <el-form-item label="促销类型" :label-width="formLabelWidth">
              <el-select v-model="form.region" placeholder="请选择促销类型" style="width:100%;">
                <el-option label="买一赠几" value="1"></el-option>
                <el-option label="第二件价格" value="3"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="关联sku" v-if="form.region == 2" :label-width="formLabelWidth">
              <el-input v-model="form.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="优惠价" v-if="form.region == 3" :label-width="formLabelWidth">
              <el-input v-model="form.preferentialPrice" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="数量" v-if="form.region == 1" :label-width="formLabelWidth">
              <el-input v-model="form.number" auto-complete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="cancelPromotionSet">取 消</el-button>
            <el-button type="primary" @click="confirmPromotionSet">确 定</el-button>
          </div>
        </el-dialog>

        <div style="border-top:1px solid #ccc;">
          <el-button class="btn-save" @click="save('data')" plain>&nbsp;&nbsp;发布&nbsp;&nbsp;</el-button>
          <el-button class="btn-cancel" plain>&nbsp;&nbsp;取消&nbsp;&nbsp;</el-button>
        </div>
      </div>
      <!-- <button v-on:click="test()">Test</button> -->
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import UE from 'util/UE.vue';
  import attrinput from 'page/oms_product/tools/attrinput.vue';

  export default {
    components: {UE, attrinput},
    data() {
      return {
        data: {
          pid: '',
          name: '',
          country: '',
          temp_type: '',
          language: '',
          domain: '',
          host: '',
          attr_list: '',
          parameter: '',
          description: '',
          img_list: [],
          key_val: [],
          sku_data: [],
        },
        dialogImageUrl: '',
        dialogVisible: false,
        dialogFormVisible: false,
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: '',
          preferentialPrice: 0,//优惠价
          number: 0//数量
        },
        formLabelWidth: '120px',
        input: '',
        countryList: [
          {
            value: 'TH',
            label: '泰国'
          },
          {
            value: 'HK',
            label: '香港'
          },
          {
            value: 'YN',
            label: '印尼'
          },
        ],
        templateList: [
          {
            value: '0',
            label: '通用模板',
          },
          {
            value: '1',
            label: '泰国模板',
          },
          {
            value: '2',
            label: '香港模板',
          },
          {
            value: '3',
            label: '印尼模板',
          },
        ],
        wwwList: [
          {
            value: '0',
            label: 'baidu.com',
          },
          {
            value: '1',
            label: 'qq.com',
          },
          {
            value: '2',
            label: 'weibo.com',
          },
        ],
        langList: [
          {
            value: '0',
            label: '泰语',
          },
          {
            value: '1',
            label: '印尼语',
          },
          {
            value: '2',
            label: '中文',
          },
        ],
        value: '',
        imageUrl: '',
        checked: false,
        checkTime: 0,
        up_show_flag: false,
        attrRowList: [],
        attrInputList: [],
        uploadParam: {
          file_field: "file",//文件上传字段 将文件流保存到该值（eg:fdfs_file）
          token: "",//加密token md5( 加密salt值 + 13位时间戳 )
          time: +new Date(),//13位时间戳
          format: "",//上传的文件格式
          file: null//文件流
        },
        releaseAllFlag: false,
        rules:{},
      };
    },
    created() {
      this.initData();
    },
    methods: {
        checkPrice(item) {
            let temp = this.data.sku_data;
            if (temp == '') {
                temp.push(item);
            } else {
                for (let node in temp) {
                    if (temp[node].id == item.id) {
                        temp.splice(node, 1);
                    }
                }
                temp.push(item);
            }
            console.log(temp);
        },
        get_value(val) {
            let tempList = this.data.key_val; 
            if (tempList == '') {
                tempList.push(val);
            } else {
                for (var item in tempList) {
                    if (tempList[item].index == val.index) {
                        tempList.splice(item, 1);
                    }
                }
                tempList.push(val);
            }
        },
      save(formName) {
        let that = this;
        this.data.description = this.$refs.ue1.getUEContent();
        this.data.parameter = this.$refs.ue2.getUEContent();
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let url = '/v1/product/releases';
            this.$axios.post(
              url,
              that.data,
            ).then((res) => {
              if (res.status == 200) {
                  this.$router.push('/release/index');
              } else {
                  this.$message.error(res.data.msg);
              }
            }).catch((err) => {
              console.log(err);
            });
          }
        });
      },
      initData() {
        let initList = [];
        let pid = this.$route.query.pid;
        let url = "/v1/release/initdatas?pid=" + pid;
        let _this = this;
        _this.$axios.get(url).then((res) => {
          let attr_list = res.data.data.attr_list;
          let attr_obj = JSON.parse(attr_list);
          _this.data.name = res.data.data.name;
          _this.data.pid = res.data.data.pid;
          for (let item in attr_obj) {
            for (let node in attr_obj[item].value) {
              let _attr = attr_obj[item].attr;
              let _value = attr_obj[item].value[node];
              let temp = {
                attr: _attr,
                value: _value
              };
              _this.attrInputList.push(temp);// 规格翻译list
            }
          }
          // 
          _this.attrRowList = res.data.data.sku_data;
        }).catch((err) => {
          console.log(err);
        })
      },
      copy(list) {
        var _list = [];
        for (var item in list) {
          _list.push(list[item]);
        }

        return _list;
      },
      beforeUpload(file) {
        this.uploadParam.token = this.$md5('salt001' + this.uploadParam.time.toString());
        let fileType = file.type;
        this.uploadParam.format = fileType.slice(fileType.indexOf('/') + 1);
        this.uploadParam.file = file;
      },
      uploadSuccess(response, file, fileList) {
        if (response.status == 0) {
          this.data.img_list.push("http://img.orkotech.com/" + response.path);
        } else {
          this.$message.error(response.msg);
        }
      },
      uploadFail(err, file, fileList) {
        this.$message.error(err);
      },
      overLimit(file, fileList) {// 超出图片限制提示
        this.$message.error('最多上传 5 张图片');
      },
      handleRemove(file, fileList) {
        let temp = 'http://img.orkotech.com/' + file.response.path;
        for (let item in this.data.img_list) {
          if (this.data.img_list[item] == temp) {
            this.data.img_list.splice(item, 1);
          }
        }
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      releaseAll() {
        let now = new Date();
        if ((now - this.checkTime) < 100) {
          return;
        }
        this.checkTime = now;
        let flag = this.$refs.ele_rel.checked;
        let ele_box = document.getElementsByClassName('release-item');
        let length = ele_box.length;
        for (let i = 0; i < length; i++) {
          let checkFlag = ele_box[i].getAttribute('aria-checked');
          if ((!flag && !checkFlag) || (flag && checkFlag)) {
            ele_box[i].click();
          }
        }
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      showUpload() {
        let now = new Date();
        if ((now - this.checkTime) < 100) {
          return;
        }
        this.checkTime = now;
        let flag = this.$refs.ele_up.isChecked;
        let temp = this.attrInputList;
        for (let item in temp) {
          if (temp[item].attr == '颜色') {
            temp[item].up_show_flag = !flag;
          }
        }
      },
      promotionSet() {
        let now = new Date();
        if ((now - this.checkTime) < 100) {
          return;
        }
        this.checkTime = now;
        let flag = this.$refs.ele_set.isChecked;
        let ele_box = document.getElementsByClassName('release-item-set');
        let length = ele_box.length;
        for (let i = 0; i < length; i++) {
          let checkFlag = ele_box[i].getAttribute('aria-checked');
          if ((!flag && !checkFlag) || (flag && checkFlag)) {
            ele_box[i].click();
          }
        }
        if (!flag) {
          this.dialogFormVisible = true;
        }
      },
      cancelPromotionSet() {
        this.dialogFormVisible = false;
        this.$refs.ele_set.model = false;
        this.$refs.promotion_set.forEach((item) => {
          item.model = false;
        })
      },
      confirmPromotionSet() {
        this.dialogFormVisible = false;
        this.$refs.promotion_set.forEach((item,index) => {
          if(item.isChecked) {
            if(this.form.region == 1){
              let number = this.form.number;
              this.attrRowList[index].info = '买1赠'+number
              this.attrRowList[index].params = number
              this.attrRowList[index].type = 1; 
            }else if(this.form.region == 2){

            }else if(this.form.region == 3) {
              let preferentialPrice = this.form.preferentialPrice;
              this.attrRowList[index].info = '第二件'+preferentialPrice
              this.attrRowList[index].params = preferentialPrice;
              this.attrRowList[index].type = 3; 
            }
          }
        })
      },
      clickSinglePromotionSet(index) {
        let now = new Date();
        if ((now - this.checkTime) < 100) {
          return;
        }
        this.checkTime = now;
        this.dialogFormVisible = true;
      }
    }
  }
</script>

<style>
  /* 设置table trhover 字体 */
  /* 字体hover主色 13c19f */
  /* hover背景色 eef1f9 */

  .name-right {
    font-size: 14px;
    line-height: 34px;
    text-align: right;
  }

  .p-title {
    margin: 10px auto;
    color: #03b8cc;
  }

  .p-content-main {
    margin-top: 20px;
  }

  .btn-save {
    float: right;
    margin: 8px;
    background-color: #13c19f;
    color: white;
    border-color: #13c19f;
    margin-right: 11%;
  }

  .btn-cancel {
    float: right;
    margin: 8px;
    margin-right: 20px;
  }

  .p-span {
    text-align: left;
    float: left;
    font-size: 14px;
    line-height: 20px;
    color: #96a6b6;
  }

  .p-span-active {
    color: black;
  }

  .el-icon-plus {
    cursor: pointer;
  }

  .attr-title {
    font-size: 14px;
    line-height: 36px;
    text-align: center;
    background: #f5f7fa;
    min-height: 36px;
    border: 1px solid #ccc;
    z-index: 10;
  }
  .attr-one {
    font-size: 14px;
    text-align: center;
    line-height: 40px;
  }

  .row-one {
    margin-top: 10px;
  }
</style>



